import React, { useState, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../hooks';
import { Button, Table, Form, FormGroup, Label, Input } from 'reactstrap';
import styled from 'styled-components';
import { studentActions, classActions } from '../../redux/actions';

const StudentsContainer = styled.div`
  padding: 20px;
`;

const FormWrapper = styled.div`
  margin-bottom: 20px;
  border: 2px solid #ccc;
  padding: 15px;
`;

const StyledButton = styled(Button)`
  margin-bottom: 20px;
`;

const StyledTableHeader = styled.th`
  background-color: #f0f0f0;
  color: #333;
`;

const StudentsManage: React.FC = () => {
    const dispatch = useAppDispatch();
    const [formVisible, setFormVisible] = useState(false);
    const [formData, setFormData] = useState({
        name: '',
        dateOfBirth: '',
        phoneNumber: '',
        fbLink: '',
        classId: '',
        startDate: '',
        id: null as number | null,
    });

    const classes = useAppSelector((state: any) => state.classes.classesList);
    const students = useAppSelector((state: any) => state.students.studentsList);

    useEffect(() => {
        dispatch(classActions.getAll());
        dispatch(studentActions.getAll());
    }, [dispatch]);

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleCreateStudent = () => {
        dispatch(studentActions.createStudent(formData));
        resetForm();
    };

    const handleUpdateStudent = () => {
        dispatch(studentActions.updateStudent(formData));
        resetForm();
    };

    const handleDeleteStudent = (studentId: number) => {
        dispatch(studentActions.deleteStudent(studentId));
        resetForm();
    };

    const handleEditStudent = (student: any) => {
        setFormData({
            name: student.name,
            dateOfBirth: student.dateOfBirth,
            phoneNumber: student.phoneNumber,
            fbLink: student.fbLink,
            classId: student.classId,
            startDate: student.startDate,
            id: student.id,
        });
        setFormVisible(true);
    };

    const resetForm = () => {
        setFormData({
            name: '',
            dateOfBirth: '',
            phoneNumber: '',
            fbLink: '',
            classId: '',
            startDate: '',
            id: null,
        });
        setFormVisible(false);
    };

    return (
        <StudentsContainer>
            <StyledButton
                color="primary"
                onClick={() => setFormVisible(true)}
                disabled={formVisible}
            >
                Thêm Học Sinh
            </StyledButton>
            {formVisible && (
                <FormWrapper>
                    <Form>
                        <FormGroup>
                            <Label for="name">Tên Học Sinh</Label>
                            <Input
                                type="text"
                                name="name"
                                id="name"
                                value={formData.name}
                                onChange={handleInputChange}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="dateOfBirth">Ngày Sinh</Label>
                            <Input
                                type="text"
                                name="dateOfBirth"
                                id="dateOfBirth"
                                value={formData.dateOfBirth}
                                onChange={handleInputChange}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="phoneNumber">Số Điện Thoại</Label>
                            <Input
                                type="text"
                                name="phoneNumber"
                                id="phoneNumber"
                                value={formData.phoneNumber}
                                onChange={handleInputChange}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="fbLink">Link Facebook</Label>
                            <Input
                                type="text"
                                name="fbLink"
                                id="fbLink"
                                value={formData.fbLink}
                                onChange={handleInputChange}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="classId">Lớp</Label>
                            <Input
                                type="select"
                                name="classId"
                                id="classId"
                                value={formData.classId}
                                onChange={handleInputChange}
                            >
                                <option value="">Chọn Lớp</option>
                                {classes.map((cls: any) => (
                                    <option key={cls.id} value={cls.id}>
                                        {cls.name}
                                    </option>
                                ))}
                            </Input>
                        </FormGroup>
                        <FormGroup>
                            <Label for="startDate">Ngày Bắt Đầu Học</Label>
                            <Input
                                type="text"
                                name="startDate"
                                id="startDate"
                                value={formData.startDate}
                                onChange={handleInputChange}
                            />
                        </FormGroup>
                        <Button
                            color="success"
                            onClick={formData.id ? handleUpdateStudent : handleCreateStudent}
                        >
                            {formData.id ? 'Cập Nhật Học Sinh' : 'Lưu Học Sinh'}
                        </Button>
                        <Button
                            color="secondary"
                            onClick={resetForm}
                        >
                            Huỷ
                        </Button>
                    </Form>
                </FormWrapper>
            )}
            <Table>
                <thead>
                    <tr>
                        <StyledTableHeader>ID</StyledTableHeader>
                        <StyledTableHeader>Tên Học Sinh</StyledTableHeader>
                        <StyledTableHeader>Ngày Sinh</StyledTableHeader>
                        <StyledTableHeader>Số Điện Thoại</StyledTableHeader>
                        <StyledTableHeader>Link Facebook</StyledTableHeader>
                        <StyledTableHeader>Ngày Bắt Đầu</StyledTableHeader>
                        <StyledTableHeader>Ngày Thêm</StyledTableHeader>
                        <StyledTableHeader>Cập Nhật</StyledTableHeader>
                    </tr>
                </thead>
                <tbody>
                    {students.map((student: any) => (
                        <tr key={student.id}>
                            <td>{student.id}</td>
                            <td>{student.name}</td>
                            <td>{student.dateOfBirth}</td>
                            <td>{student.phoneNumber}</td>
                            <td><a href={student.fbLink} target="_blank" rel="noopener noreferrer">here</a></td>
                            <td>{student.startDate}</td>
                            <td>{student.createdDate}</td>
                            <td>
                                <Button color="warning" onClick={() => handleEditStudent(student)}>Sửa</Button>
                                <Button color="danger" onClick={() => handleDeleteStudent(student.id)}>Xoá</Button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </Table>
        </StudentsContainer>
    );
};

export default StudentsManage;
