import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { RootState } from '../../store';
import { Button, Table, Container, Form, FormGroup, Label, Input } from 'reactstrap';
import styled from 'styled-components';
import { classActions, userActions } from '../../redux/actions';
import { useAppDispatch, useAppSelector } from '../../hooks';

const ClassesContainer = styled.div`
  padding: 20px;
`;

const FormWrapper = styled.div`
  margin-bottom: 20px;
  border: 2px solid #ccc;
  padding: 15px;
`;

const StyledButton = styled(Button)`
  margin-bottom: 20px;
`;

const StyledTableHeader = styled.th`
  background-color: #f0f0f0; /* Thay đổi màu nền tiêu đề */
  color: #333; /* Thay đổi màu chữ */
`;
interface Teacher {
    id: number;
    userName: string;
    email: string;
    role: number;
}

const ClassesManage: React.FC = () => {
    const dispatch = useAppDispatch();
    const [formVisible, setFormVisible] = useState(false);
    const [formData, setFormData] = useState({
        name: '',
        teacherId: '',
        sessionFee: '',
        monthlyFee: '',
        note: '',
        id: null as number | null,
    });
    const classes = useAppSelector((state: any) => state.classes.classesList);
    const teachers: Teacher[] = useAppSelector((state: any) => state.users.items);

    useEffect(() => {
        dispatch(userActions.getAllTeachers());
        dispatch(classActions.getAll());
    }, [dispatch]);

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const getTeacherUserName = (teacherId: number): string | undefined => {
        const teacher = teachers.find(t => t.id === teacherId);
        return teacher ? teacher.userName : undefined;
    }

    const handleCreateClass = () => {
        dispatch(classActions.createClass(formData));
        resetForm();
    };

    const handleUpdateClass = () => {
        dispatch(classActions.updateClass(formData));
        resetForm();
    };

    const handleDeleteClass = (classID: number) => {
        dispatch(classActions.deleteClass(classID));
        resetForm();
    };

    const handleEditClass = (cls: any) => {
        setFormData({
            name: cls.name,
            teacherId: cls.teacherId,
            sessionFee: cls.sessionFee,
            monthlyFee: cls.monthlyFee,
            note: cls.note || '',
            id: cls.id,
        });
        setFormVisible(true);
    };

    const resetForm = () => {
        setFormData({
            name: '',
            teacherId: '',
            sessionFee: '',
            monthlyFee: '',
            note: '',
            id: null,
        });
        setFormVisible(false);
    };

    return (
        <ClassesContainer>
            <StyledButton
                color="primary"
                onClick={() => setFormVisible(true)}
                disabled={formVisible}
            >
                Tạo Lớp Học
            </StyledButton>
            {formVisible && (
                <FormWrapper>
                    <Form>
                        <FormGroup>
                            <Label for="name">Tên Lớp</Label>
                            <Input
                                type="text"
                                name="name"
                                id="name"
                                value={formData.name}
                                onChange={handleInputChange}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="teacherId">Giáo viên :</Label>
                            <Input
                                type="select"
                                name="teacherId"
                                id="teacherId"
                                value={formData.teacherId}
                                onChange={handleInputChange}
                            >
                                <option value="">Chọn Giáo viên</option>
                                {teachers ? teachers.map((teacher: any) => (
                                    <option key={teacher.id} value={teacher.id}>
                                        {teacher.userName}
                                    </option>
                                )) : null}
                            </Input>
                        </FormGroup>
                        <FormGroup>
                            <Label for="sessionFee">Học phí 1 buổi (Nếu có):</Label>
                            <Input
                                type="number"
                                name="sessionFee"
                                id="sessionFee"
                                value={formData.sessionFee}
                                onChange={handleInputChange}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="monthlyFee">Học phí 1 tháng (Nếu có):</Label>
                            <Input
                                type="number"
                                name="monthlyFee"
                                id="monthlyFee"
                                value={formData.monthlyFee}
                                onChange={handleInputChange}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="notes">Ghi chú</Label>
                            <Input
                                type="textarea"
                                name="note"
                                id="note"
                                value={formData.note}
                                onChange={handleInputChange}
                            />
                        </FormGroup>
                        <Button
                            color="success"
                            onClick={formData.id ? handleUpdateClass : handleCreateClass}
                        >
                            {formData.id ? 'Cập Nhật Lớp Học' : 'Lưu Lớp Học'}
                        </Button>
                        <Button
                            color="secondary"
                            onClick={resetForm}
                        >
                            Huỷ
                        </Button>
                    </Form>
                </FormWrapper>
            )}
            <Table>
                <thead>
                    <tr>
                        <StyledTableHeader>ID</StyledTableHeader>
                        <StyledTableHeader>Tên Lớp</StyledTableHeader>
                        <StyledTableHeader>Tên GVCN</StyledTableHeader>
                        <StyledTableHeader>Học Phí theo buổi</StyledTableHeader>
                        <StyledTableHeader>Học Phí theo tháng</StyledTableHeader>
                        <StyledTableHeader>Ghi chú</StyledTableHeader>
                        <StyledTableHeader>Cập Nhật</StyledTableHeader>
                    </tr>
                </thead>
                <tbody>
                    {classes.map((cls: any) => (
                        <tr key={cls.id}>
                            <td>{cls.id}</td>
                            <td>{cls.name}</td>
                            <td>{getTeacherUserName(cls.teacherId)}</td>
                            <td>{cls.sessionFee}</td>
                            <td>{cls.monthlyFee}</td>
                            <td>{cls.note}</td>
                            <td>
                                <Button color="warning" onClick={() => handleEditClass(cls)}>Sửa</Button>
                                <Button color="danger" onClick={() => handleDeleteClass(cls.id)}>Xoá</Button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </Table>
        </ClassesContainer>
    );
};

export default ClassesManage;
